.mainclass{
    width: 100%;
    border-radius: 10px;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.3);
}
main{
    /* border: 2px solid black; */
    /* background:linear-gradient(top,red,blue); */
    min-height: 100vh;
    padding:5px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 10px;
    width: 100%;
    overflow: hidden;
    background-repeat: no-repeat;
    background: linear-gradient(-45deg, #b9958a, #c6819b, #90bac9, #96c6bb);
    background-size: 400% 400%;
    animation: gradient 10s ease infinite;
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}
.text >p{
    background-color: inherit;
    word-wrap: break-word;
    font-size: 1.4rem;
    padding-left: 20px;
}
.mainclass>h1{
    text-align: center;
    background-color: rgba(0, 0, 0,0.1 );
    color:rgb(225, 225, 225);
  width: 100%;
}
.text{
background-color: rgba(0, 0, 0, 0);
color:rgb(200, 197, 197);
padding:2px;
width:100%;
}
.textout{
    background-color: rgba(0, 0, 0, 0);  
    width: 100%;
}
.profile{
display: flex;
align-items: center;
justify-content: center;
background-color: inherit;
margin-bottom: 15px;
padding-right: 0px;
padding:0px 20px 0px 20px;
background-color: rgba(255, 255, 255, 0)
}
.profile>img{
    width: 150px;
    border-radius: 60%;
}
.socials{
    display: flex;
align-items: center;
justify-content: center;
background-color: rgba(0, 0, 0, 0);
margin-top: 5px;
margin-bottom: 5px;
padding: 5px;
}
.socials img{
    width: 50px;
    background-color: rgba(0, 0, 0, 0.1);
}
@media(max-width:673px){
main{
    background-size: cover;
    background-position: -300px;
}
}
#im{
   width: 200px;
    animation-name: example;
    animation-duration: 2s;
    /* animation-iteration-count: infinite; */
    animation-fill-mode: forwards;
    margin-top: 30px;
}
@keyframes example {
    0%   {border-radius: 0px;}
    25%   {border-radius: 0px 10px 15px 20px;}
    50%   {border-radius: 50px;transform: translate(70px);}
 75%{border-radius: 100px;padding:0px;background-color: rgb(0, 0, 0);}
    100% {border-radius: 200px;padding:10px;background-color: rgb(255, 255, 255);transform: translate(-10px)}
  }